* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/* Base Styles */
html, body {
  font-size: 18px;
  font-family: Source Sans Pro, Arial, sans-serif;
  color: #212121;
  background-color: #fafafa;
  min-height: 100vh;
}

h1, .h1 {
  font-size: 3.8rem;
  font-family: Montserrat, Verdana, Arial, sans-serif;
  margin-bottom: 0.5rem;
}

h2, .h2 {
  font-size: 3rem;
  font-family: Montserrat, Verdana, Arial, sans-serif;
  margin-bottom: 0.5rem;
}

h3, .h3 {
  font-size: 2.2rem;
  font-family: Montserrat, Verdana, Arial, sans-serif;
  margin-bottom: 0.5rem;
}

h4, .h4 {
  font-size: 2rem;
  font-family: Montserrat, Verdana, Arial, sans-serif;
  margin-bottom: 0.5rem;
}

h5, .h5 {
  font-size: 1.5rem;
  font-family: Montserrat, Verdana, Arial, sans-serif;
  margin-bottom: 0.5rem;
}

h6, .h6 {
  font-size: 1rem;
  font-family: Montserrat, Verdana, Arial, sans-serif;
  margin-bottom: 0.5rem;
}

a, a:visited, a:focus {
  color: #2196f3;
  text-decoration: none;
}

a:hover {
  color: #1976d2;
}

img {
  display: block;
}

strong, b {
  font-weight: bold;
}

em {
  font-style: italic;
}

small {
  font-size: 0.8rem;
}

header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: #fafafa;
  padding: 0.2rem;
}

nav ul {
  list-style: none;
}

nav ul li {
  display: inline-block;
}

main {
  min-height: 100vh;
}

section.page {
  margin: 0 1rem 1rem 1rem;
}

table {
  border-collapse: collapse;
}

th, td {
  padding: 0.5rem;
  vertical-align: middle;
  text-align: left;
}

input, select {
  display: inline-block;
  width: 100%;
  height: 30px;
  background-color: #fff;
  border: thin solid #e0e0e0;
  border-radius: 0.2rem;
}

button {
  color: #fff;
  background-color: #2196f3;
  padding: 0.5rem;
  -webkit-box-shadow: 3px 3px 5px rgba(1, 1, 1, 0.3);
          box-shadow: 3px 3px 5px rgba(1, 1, 1, 0.3);
  border-radius: 0.2rem;
}

button:hover {
  cursor: pointer;
  background-color: #1976d2;
}

button.link {
  color: #2196f3;
  background-color: transparent;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-radius: none;
}

button.link:hover {
  cursor: pointer;
  color: #1976d2;
}

button i {
  margin-right: 0.25rem;
}

button.gray {
  color: #fff;
  background-color: #9e9e9e;
}

button.gray:hover {
  background-color: #616161;
}

button.amber {
  color: #000;
  background-color: #ffc107;
}

button.amber:hover {
  background-color: #FFA000;
}

button.yellow {
  color: #000;
  background-color: #ffeb3b;
}

button.yellow:hover {
  background-color: #FBC02D;
}

button.settings {
  height: 56px;
  width: 56px;
  padding: 1rem;
  border-radius: 50%;
  position: fixed;
  right: 16px;
  bottom: 16px;
}

button.settings:hover {
  cursor: pointer;
  background-color: #1976d2;
}

/* Specific Content */
.copyright {
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  background-color: #e0e0e0;
  border-radius: 0.2rem;
  font-family: Source Code Pro, Courier New, monospace;
  font-size: 0.8rem;
}

.cards {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
}

.card {
  background: #fff;
  padding: 0.5rem;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  -webkit-box-shadow: 3px 3px 5px rgba(1, 1, 1, 0.3);
          box-shadow: 3px 3px 5px rgba(1, 1, 1, 0.3);
}

.passage {
  margin-left: 1rem;
}

/* Helper Functions */
.font-heading {
  font-family: Montserrat, Verdana, Arial, sans-serif;
}

.font-body {
  font-family: Source Sans Pro, Arial, sans-serif;
}

.font-monospace {
  font-family: Source Code Pro, Courier New, monospace;
}

.text-blue {
  color: #2196f3;
}

.bg-blue {
  background-color: #2196f3;
}

.text-red {
  color: #F44336;
}

.bg-red {
  background-color: #F44336;
}

.text-gray {
  color: #9e9e9e;
}

.bg-gray {
  background-color: #9e9e9e;
}

.text-amber {
  color: #ffc107;
}

.bg-amber {
  background-color: #ffc107;
}

.text-yellow {
  color: #ffeb3b;
}

.bg-yellow {
  background-color: #ffeb3b;
}

.text-black {
  color: #000;
}

.bg-black {
  background-color: #000;
}

.text-white {
  color: #fff;
}

.bg-white {
  background-color: #fff;
}

.text-muted {
  color: #9e9e9e;
  font-style: italic;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.p {
  padding: 0.5rem;
}

.mr {
  margin-right: 0.5rem;
}

/* Text Sizes */
.passage.size-regular {
  font-size: 1rem;
}

.passage.size-medium {
  font-size: 2rem;
}

.passage.size-large {
  font-size: 3rem;
}

/* Color Modes */
/* Night Mode */
main.night-mode,
main.night-mode header {
  color: #e0e0e0;
  background-color: #212121;
}

main.night-mode .card {
  color: #e0e0e0;
  background-color: #424242;
}

main.night-mode .copyright {
  color: #fff;
  background-color: #000;
}

main.night-mode form input,
main.night-mode form select,
main.night-mode form textarea {
  color: #e0e0e0;
  background-color: #212121;
  border: none;
}

